import Vue from 'vue';

Vue.directive('meu-transform', {

    bind(el, binding, vnode) {
         //   Eixo padro= current pode ser qualquer nome
         let current = 0;

        // adicionando um evento ao elemento do DOM, clicando duas vezes ativa a funco
        el.addEventListener('dblclick', function() {

            let incremento = binding.value || 90;
            let efeito;
        // Se no foi passado o argumento ou  igual a rotate ele vai fazer:
            if(!binding.arg || binding.arg =='rotate'){
                
                if(binding.modifiers.reverse) {
                    current-=incremento;
                  } else {
                    current+=incremento;
                  }
                  efeito = `rotate(${current}deg)`;    
            }else if(binding.arg == "scale"){
                efeito = `scale(${incremento})`
            }
    
        // para aplicar a rotao
        this.style.transform = efeito;
        if (binding.modifiers.animate) this.style.transition = "transform 0.5s";

      });
    }

});



     


